@use '../variables';

.infos-bar{
    padding: 16px 24px;
    background-color: variables.$textColor;
    display: flex;
    justify-content: space-between;

    &__infos{
        display: flex;
        gap: 40px;

        &__item{
            font-size: 14px;
            color: variables.$secondaryTextColor;

            b{
                color: variables.$secondaryTextColor;
                font-size: 18px;
                display: block;
                padding: 8px;
            }
        }
    }

    @media (max-width: 640px){
        display: block;

        &__infos{
            display: block;

            &__item{
                margin-bottom: 24px;
            }
        }
    }

    @media(max-width: 1024px){
        &__infos{
            gap: 16px;
        }
    }
}
