@use 'variables';

.hero{
    padding-top: 100px;
    padding-bottom: 150px;
    background-image: url(../images/balloons-1869790_1920.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .container{
        position: relative;
    }

    &::before{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: variables.$secondaryTextColor;
        content: '';
        opacity: 0.7;
    }

    &__text{
        padding-top: 64px;
        padding-bottom: 64px;
        font-size: 40px;
        line-height: 56px;
        font-weight: 800;
        text-align: center;
    }

    &__title{
        margin: 0 auto;
        display: block;
        font-size: 40px;
        line-height: 56px;
        font-weight: 800;
        text-align: center;
    }

    @media (max-width: 640px){
        padding: 40px 0;
        &__logo{
            height: 40px;
        }

        &__text{
            font-size: 22px;
            line-height: 24px;
            margin: 24px 0;
        }
    }
}